            a {
                color: #162E36;
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
                text-decoration: none;
                font-family: "Gotham";
                margin-right: 38.4px;
            }

            button {
                font-family: Arial, Helvetica, sans-serif;
                width: 208px;
                height: 44px;
                background-color: #289225;
                color: white;
                border: none;
                font-size: 15px;
            }
        }

        .burgerMenu {
            display: none;
            #menu__toggle {
                opacity: 0;
            }
    
            .menu__btn {
                display: flex;
                align-items: center;
                position: relative;
    
                width: 26px;
                height: 26px;
    
                cursor: pointer;
                z-index: 1;
            }
    
            .menu__btn > span,
            .menu__btn > span::before,
            .menu__btn > span::after {
                display: block;
                position: absolute;
    
                width: 100%;
                height: 3.2px;
    
                background: #289225;
            }
    
            .menu__btn > span::before {
                content: "";
                top: -8px;
            }
    
            .menu__btn > span::after {
                content: "";
                top: 8px;
            }
    
            // style for menu
    
            .menu__box {
                display: block;
                position: fixed;
                visibility: hidden;
                top: 0;
                left: -100%;
                width:100%;
                height: 100%;
                margin: 0;
                padding: 50.4px 0;
                list-style: none;
                text-align: left;
                background-color: white;
                box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
    
                .menu__item {
                    display: block;
                    padding: 16px 24px;
                    color: #333;
                    font-family: 'Roboto', sans-serif;
                    font-size: 28px;
                    font-weight: 600;
                    text-decoration: none;
                }
    
                .menu__item:hover {
                    background-color: #CFD8DC;
                }
    
                .logo2 {
                    width: 35px;
                    height: 38px;

                    margin: 0px 24px;
                    background-image: url(../images/Frame\ 41.svg);
                    background-size: cover;
                    margin-bottom: 38px;
                }
            }
    
            #menu__toggle:checked ~ .menu__btn > span {
                transform: rotate(45deg);
              }
              #menu__toggle:checked ~ .menu__btn > span::before {
                top: 0;
                transform: rotate(0);
              }
              #menu__toggle:checked ~ .menu__btn > span::after {
                top: 0;
                transform: rotate(90deg);
              }
              #menu__toggle:checked ~ .menu__box {
                visibility: visible;
                left: 0;
              }
    
              .menu__btn > span,
              .menu__btn > span::before,
              .menu__btn > span::after {
                transition-duration: .25s;
               }
              .menu__box {
               transition-duration: .25s;
               }
               .menu__item {
                transition-duration: .25s;
               }

               .menu__contacts {
                p {
                    color: var(--green, #289225);
                    font-family: Arial, Helvetica, sans-serif;
                    font-size: 20px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: normal;
                    padding: 0px 24px;
                    margin-top: 51px;
                }

                .menu__textContacts {
                    margin-top: 25px;
                }
                
               }
        }
